.cssload-thecube-container {
  margin:0;
  position: fixed;
  background: rgba(0, 0, 0, 0.753);
  z-index: 99999999;
  padding: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.cssload-thecube {
  width: 66px;
  height: 66px;
  margin: auto;
  position: fixed;
  top: 50%;
  left: 50%; 
  margin-top: -33px;
  margin-left: -33px;
  transform: rotateZ(45deg);
  -o-transform: rotateZ(45deg);
  -ms-transform: rotateZ(45deg);
  -webkit-transform: rotateZ(45deg);
  -moz-transform: rotateZ(45deg);
}

.cssload-thecube-container-list {
  width: 100%;
  padding-top: 150%;
}

.cssload-thecube-list {
  width: 66px;
  height: 66px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 auto;
  margin-top: -33px;
  margin-left: -33px;
  transform: rotateZ(45deg);
  -o-transform: rotateZ(45deg);
  -ms-transform: rotateZ(45deg);
  -webkit-transform: rotateZ(45deg);
  -moz-transform: rotateZ(45deg);
}

.cssload-thecube .cssload-cube {
  position: relative;
  transform: rotateZ(45deg);
  -o-transform: rotateZ(45deg);
  -ms-transform: rotateZ(45deg);
  -webkit-transform: rotateZ(45deg);
  -moz-transform: rotateZ(45deg);
}

.cssload-thecube .cssload-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
}

.cssload-thecube .cssload-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #7a2365;
  animation: cssload-fold-thecube 1.92s infinite linear both;
  -o-animation: cssload-fold-thecube 1.92s infinite linear both;
  -ms-animation: cssload-fold-thecube 1.92s infinite linear both;
  -webkit-animation: cssload-fold-thecube 1.92s infinite linear both;
  -moz-animation: cssload-fold-thecube 1.92s infinite linear both;
  transform-origin: 100% 100%;
  -o-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  -webkit-transform-origin: 100% 100%;
  -moz-transform-origin: 100% 100%;
}

.cssload-thecube .cssload-c2 {
  transform: scale(1.1) rotateZ(90deg);
  -o-transform: scale(1.1) rotateZ(90deg);
  -ms-transform: scale(1.1) rotateZ(90deg);
  -webkit-transform: scale(1.1) rotateZ(90deg);
  -moz-transform: scale(1.1) rotateZ(90deg);
}

.cssload-thecube .cssload-c3 {
  transform: scale(1.1) rotateZ(180deg);
  -o-transform: scale(1.1) rotateZ(180deg);
  -ms-transform: scale(1.1) rotateZ(180deg);
  -webkit-transform: scale(1.1) rotateZ(180deg);
  -moz-transform: scale(1.1) rotateZ(180deg);
}

.cssload-thecube .cssload-c4 {
  transform: scale(1.1) rotateZ(270deg);
  -o-transform: scale(1.1) rotateZ(270deg);
  -ms-transform: scale(1.1) rotateZ(270deg);
  -webkit-transform: scale(1.1) rotateZ(270deg);
  -moz-transform: scale(1.1) rotateZ(270deg);
}

.cssload-thecube .cssload-c2:before {
  animation-delay: 0.235s;
  -o-animation-delay: 0.235s;
  -ms-animation-delay: 0.235s;
  -webkit-animation-delay: 0.235s;
  -moz-animation-delay: 0.235s;
}

.cssload-thecube .cssload-c3:before {
  animation-delay: 0.48s;
  -o-animation-delay: 0.48s;
  -ms-animation-delay: 0.48s;
  -webkit-animation-delay: 0.48s;
  -moz-animation-delay: 0.48s;
}

.cssload-thecube .cssload-c4:before {
  animation-delay: 0.715s;
  -o-animation-delay: 0.715s;
  -ms-animation-delay: 0.715s;
  -webkit-animation-delay: 0.715s;
  -moz-animation-delay: 0.715s;
}

.cssload-thecube-list .cssload-cube {
  position: relative;
  transform: rotateZ(45deg);
  -o-transform: rotateZ(45deg);
  -ms-transform: rotateZ(45deg);
  -webkit-transform: rotateZ(45deg);
  -moz-transform: rotateZ(45deg);
}

.cssload-thecube-list .cssload-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
}

.cssload-thecube-list .cssload-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #7a2365;
  animation: cssload-fold-thecube 1.92s infinite linear both;
  -o-animation: cssload-fold-thecube 1.92s infinite linear both;
  -ms-animation: cssload-fold-thecube 1.92s infinite linear both;
  -webkit-animation: cssload-fold-thecube 1.92s infinite linear both;
  -moz-animation: cssload-fold-thecube 1.92s infinite linear both;
  transform-origin: 100% 100%;
  -o-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  -webkit-transform-origin: 100% 100%;
  -moz-transform-origin: 100% 100%;
}

.cssload-thecube-list .cssload-c2 {
  transform: scale(1.1) rotateZ(90deg);
  -o-transform: scale(1.1) rotateZ(90deg);
  -ms-transform: scale(1.1) rotateZ(90deg);
  -webkit-transform: scale(1.1) rotateZ(90deg);
  -moz-transform: scale(1.1) rotateZ(90deg);
}

.cssload-thecube-list .cssload-c3 {
  transform: scale(1.1) rotateZ(180deg);
  -o-transform: scale(1.1) rotateZ(180deg);
  -ms-transform: scale(1.1) rotateZ(180deg);
  -webkit-transform: scale(1.1) rotateZ(180deg);
  -moz-transform: scale(1.1) rotateZ(180deg);
}

.cssload-thecube-list .cssload-c4 {
  transform: scale(1.1) rotateZ(270deg);
  -o-transform: scale(1.1) rotateZ(270deg);
  -ms-transform: scale(1.1) rotateZ(270deg);
  -webkit-transform: scale(1.1) rotateZ(270deg);
  -moz-transform: scale(1.1) rotateZ(270deg);
}

.cssload-thecube-list .cssload-c2:before {
  animation-delay: 0.235s;
  -o-animation-delay: 0.235s;
  -ms-animation-delay: 0.235s;
  -webkit-animation-delay: 0.235s;
  -moz-animation-delay: 0.235s;
}

.cssload-thecube-list .cssload-c3:before {
  animation-delay: 0.48s;
  -o-animation-delay: 0.48s;
  -ms-animation-delay: 0.48s;
  -webkit-animation-delay: 0.48s;
  -moz-animation-delay: 0.48s;
}

.cssload-thecube-list .cssload-c4:before {
  animation-delay: 0.715s;
  -o-animation-delay: 0.715s;
  -ms-animation-delay: 0.715s;
  -webkit-animation-delay: 0.715s;
  -moz-animation-delay: 0.715s;
}

@keyframes cssload-fold-thecube {
  0%,
  10% {
    -webkit-transform: perspective(122px) rotateX(-180deg);
            transform: perspective(122px) rotateX(-180deg);
    opacity: 0;
  }
  25%,
  75% {
    -webkit-transform: perspective(122px) rotateX(0deg);
            transform: perspective(122px) rotateX(0deg);
    opacity: 1;
  }
  90%,
  100% {
    -webkit-transform: perspective(122px) rotateY(180deg);
            transform: perspective(122px) rotateY(180deg);
    opacity: 0;
  }
}

@-webkit-keyframes cssload-fold-thecube {
  0%,
  10% {
    -webkit-transform: perspective(122px) rotateX(-180deg);
    opacity: 0;
  }
  25%,
  75% {
    -webkit-transform: perspective(122px) rotateX(0deg);
    opacity: 1;
  }
  90%,
  100% {
    -webkit-transform: perspective(122px) rotateY(180deg);
    opacity: 0;
  }
}


